<template>
    <v-content>
        <v-container fluid>
            <v-row>
                <v-col cols="12">
                    <h4 class="headline">LGU Users</h4>
                    <v-breadcrumbs :items="breadcrumbs">
                        <template v-slot:divider>
                            <v-icon>mdi-chevron-right</v-icon>
                        </template>
                    </v-breadcrumbs>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <div class="mb-5 text-right">
                        <v-dialog v-model="dialog2" persistent max-width="600px">
                            <v-card>
                                <v-card-title>
                                    <span class="headline">LGU Profile</span>
                                </v-card-title>
                                <v-form lazy-validation ref="form">
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Name *" v-model="editData.name" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="E-mail *" v-model="editData.email" type="email"
                                                                  required></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Contact No. *" v-model="editData.contact_no" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-select v-model="editData.province" :items="province"
                                                              label="Province *" required></v-select>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                        <small>*indicates required field</small>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="dialog2 = false">Close</v-btn>
                                        <v-btn color="blue darken-1" text @click="update()">Save</v-btn>
                                    </v-card-actions>
                                </v-form>
                            </v-card>
                        </v-dialog>
                        <v-dialog v-model="dialog" persistent max-width="600px">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn color="primary" dark v-bind="attrs" v-on="on">
                                    Add new user
                                </v-btn>
                            </template>
                            <v-card>
                                <v-card-title>
                                    <span class="headline">LGU Profile</span>
                                </v-card-title>
                                <v-form lazy-validation ref="form">
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Name *" v-model="data.name" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field
                                                        label="Password *"
                                                        v-model="data.password"
                                                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                                        :type="show1 ? 'text' : 'password'"
                                                        @click:append="show1 = !show1"
                                                        required
                                                        ></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="E-mail *" v-model="data.email" type="email"
                                                        required></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Contact No. *" v-model="data.contact" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-select v-model="data.province" :items="province"
                                                        label="Province *" required></v-select>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                        <small>*indicates required field</small>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
                                        <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                                    </v-card-actions>
                                </v-form>
                            </v-card>
                        </v-dialog>
                    </div>
                    <v-card class="mx-auto">
                        <v-simple-table dense class="px-2 py-2">
                            <template v-slot:default>
                                <thead>
                                    <tr>
                                        <th class="text-left">No.</th>
                                        <th class="text-left">Name</th>
                                        <th class="text-left">Address</th>
                                        <th class="text-left">Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="person in lgu" :key="person.id">
                                        <td>{{ person.id }}</td>
                                        <td>{{ person.name }}</td>
                                        <td>{{ person.province }}</td>
                                        <td>
                                            <div class="my-1">
                                                <v-btn small color="success" class="mx-2" @click="edit(person.id)">Edit</v-btn>
                                                <v-btn v-if="person.isActive == 1" small color="error" @click="disable(person.id)">
                                                    Set inactive
                                                </v-btn>
                                                <v-btn v-if="person.isActive == 0" small color="secondary" @click="enable(person.id)">
                                                    Set Active
                                                </v-btn>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </template>
                        </v-simple-table>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
        <v-snackbar v-model="snackbar" :color="color" :top="true" :vertical="true">
            {{ snackbarText }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-content>
</template>

<script>
    import randomstring from 'randomstring'
    export default {
        name: 'Lgu',
        data() {
            return {
                breadcrumbs: [{
                        text: 'Home',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Dashboard',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Users',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'LGU',
                        disabled: true,
                        href: '#'
                    }
                ],
                snackbar: false,
                snackbarText: '',
                color: "#E53935",
                data: {
                    name: '',
                    email: '',
                    contact: '',
                    province: '',
                    password: ''
                },
                editData: {
                    id: '',
                    name: '',
                    email: '',
                    contact_no: '',
                    province: ''
                },
                dialog: false,
                dialog2: false,
                province: {},
                errors: [],
                lgu: [],
                show1: false,
            }
        },
        methods: {
            save() {
                this.$store.dispatch('saveLGU', this.data)
                    .then(response => {
                        if(response.data.error){
                            this.snackbarText = ''
                            for(let [key,value] of Object.entries(response.data.error))
                            {
                                console.log("key", key)
                                this.snackbarText += value[0] + '. '

                            }

                            this.snackbar = true
                            return false
                        }

                        this.lgu.push(response.data.lgu)

                        this.color = '#3CB371',
                        this.snackbarText = response.data.message
                        this.snackbar = true
                        this.data.name = ''
                        this.data.email = ''
                        this.data.contact = ''
                        this.data.province = ''
                        this.data.password = ''
                    })
                    .catch((err) => {
                        console.log(err)
                    })
                this.dialog = false
            },
            enable(id)
            {
                 this.$store.dispatch('setActive', id)
                    .then(response => {
                        console.log(response.data)
                        this.lgu.find(person => person.id === id).isActive = 1
                    })
            },
            disable(id)
            {
                this.$store.dispatch('setInactive', id)
                    .then(response => {
                        console.log(response)
                        this.lgu.find(person => person.id === id).isActive = 0
                    })
            },
            edit(id){
                this.editData.id = id
                this.editData.name = this.lgu.find(person => person.id === id).name
                this.editData.contact_no = this.lgu.find(person => person.id === id).contact_no
                this.editData.email = this.lgu.find(person => person.id === id).email
                this.editData.province = this.lgu.find(person => person.id === id).province
                this.dialog2 = true
            },
            update()
            {
                this.$store.dispatch('updateLGU', this.editData)
                    .then(response => {
                        if(response.data.status === false) return false
                        this.lgu.find(person => person.id === this.editData.id).name = this.editData.carrier
                        this.lgu.find(person => person.id === this.editData.id).contact_no = this.editData.contact
                        this.lgu.find(person => person.id === this.editData.id).email = this.editData.address
                        this.lgu.find(person => person.id === this.editData.id).province = this.editData.province

                    })
                this.dialog2 = false
            }
        },
        async created() {
            this.province = await this.$store.dispatch('fetchProvince')

            this.province = this.province.data
            this.province.sort()

            this.lgu = await this.$store.dispatch('fetchLGU')
            this.lgu = this.lgu.data

            this.data.password = randomstring.generate(12)
        }
    }
</script>

<style>

</style>
