<template>
    <v-content>
        <v-container fluid>

            <v-row>
                <v-col cols="12">
                    <h4 class="headline">Dashboard</h4>
                    <v-breadcrumbs :items="breadcrumbs">
                        <template v-slot:divider>
                            <v-icon>mdi-chevron-right</v-icon>
                        </template>
                    </v-breadcrumbs>
                </v-col>
            </v-row>
            <v-row>
                <v-col
                cols="4">
                    <v-card light>
                        <div class="d-flex flex-no-wrap justify-space-between">
                            <div>
                                <v-card-title v-show="today"
                                class="display-1 red--text text--lighten-1"
                                >{{ profileTodaycount }}</v-card-title>

                                <v-card-title v-show="spinner1">
                                    <v-progress-circular
                                        indeterminate
                                        color="primary"
                                    ></v-progress-circular>
                                </v-card-title>

                                <v-card-subtitle>Profiles save today</v-card-subtitle>
                            </div>
                            <v-avatar class="ma-5" color="amber">
                                <v-icon dark>mdi-account-arrow-right-outline</v-icon>
                            </v-avatar>
                        </div>
                    </v-card>
                </v-col>

                <v-col
                cols="4">
                    <v-card light>
                        <div class="d-flex flex-no-wrap justify-space-between">
                            <div>
                                <v-card-title v-show="average"
                                class="display-1 red--text text--lighten-1"
                                >{{ aveProfile }}</v-card-title>

                                <v-card-title v-show="spinner2">
                                    <v-progress-circular
                                        indeterminate
                                        color="primary"
                                    ></v-progress-circular>
                                </v-card-title>

                                <v-card-subtitle>Average profiles per day</v-card-subtitle>
                            </div>

                            <v-avatar class="ma-5" color="green">
                                <v-icon dark>mdi-account-box-multiple</v-icon>
                            </v-avatar>
                        </div>
                    </v-card>
                </v-col>

                <v-col
                cols="4">
                    <v-card light>
                        <div class="d-flex flex-no-wrap justify-space-between">
                            <div>
                                <v-card-title v-show="total"
                                class="display-1 red--text text--lighten-1"
                                >{{ totalRecords }}</v-card-title>

                                <v-card-title v-show="spinner3">
                                    <v-progress-circular
                                        indeterminate
                                        color="primary"
                                    ></v-progress-circular>
                                </v-card-title>

                                <v-card-subtitle>Total Profiles Traced</v-card-subtitle>
                            </div>

                            <v-avatar class="ma-5" color="red darken-2">
                                <v-icon dark>mdi-account-group</v-icon>
                            </v-avatar>
                        </div>
                    </v-card>
                </v-col>
            </v-row>
<!--            <google-map-component></google-map-component>-->
            <v-row>
                <v-col cols="6">
                    <v-card light>
                        <div class="pa-3">
                            <line-chart :data="dataLineChart"></line-chart>
                        </div>
                    </v-card>
                </v-col>
                <v-col cols="6">
                    <v-card light>
                        <div class="pa-3">
                            <bar-chart :data="dataBarChart"></bar-chart>
                        </div>
                    </v-card>
                </v-col>
            </v-row>

        </v-container>
    </v-content>
</template>

<script>

    import { mapActions, mapGetters } from 'vuex'
    //import VueApexCharts from 'apexcharts'
    //import GoogleMapComponent from '@/components/GoogleMap.vue'

    export default {
        name: 'Dashboard',
        components: {
            //'apexchart': VueApexCharts
        },
        data() {
            return {
                breadcrumbs: [
                    {
                        text: 'Home',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Dashboard',
                        disabled: true,
                        href: '#'
                    },
                ],
                dataLineChart: [],
                dataBarChart: [],
                aveProfile: 0,
                profileTodaycount: 0,
                totalRecords: 0,
                today: false,
                average: false,
                total: false,
                spinner1: true,
                spinner2: true,
                spinner3: true,
            }
        },
        methods: {
            ...mapActions(['fetchAverageProfile', 'fetchProfileToday', 'fetchTotalRecords']),

        },
        computed: mapGetters(['averageProfile', 'profileToday']),
        async created(){

            await this.fetchProfileToday()
            this.spinner1 = false
            this.profileTodaycount = (this.profileToday) ? this.profileToday : 0
            this.today = true

            await this.fetchAverageProfile()
            this.spinner2 = false
            this.aveProfile = (this.averageProfile.average) ? this.averageProfile.average.toFixed(2) : 0.0
            this.average = true

            const response = await this.fetchTotalRecords()
            this.spinner3 = false
            this.totalRecords = response.data.total
            this.total = true

            const res = await this.$store.dispatch('profilePerDay')
            this.dataLineChart = res.data

            const raw = await this.$store.dispatch('forBarChart')
            const test = raw.data

            this.dataBarChart = Object.keys(test).map(function(key){
                return [key, test[key]]
            })

        }
    }
</script>

<style>

</style>
