<template>
    <v-content>
        <v-container fluid>
            <v-row>
                <v-col cols="12">
                    <h4 class="headline">Users</h4>
                    <v-breadcrumbs :items="breadcrumbs">
                        <template v-slot:divider>
                            <v-icon>mdi-chevron-right</v-icon>
                        </template>
                    </v-breadcrumbs>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <div class="mb-5 text-right">
                        <v-dialog v-model="dialog" persistent max-width="600px">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn color="primary" dark v-bind="attrs" v-on="on">
                                    Add new Users
                                </v-btn>
                            </template>
                            <v-card>
                                <v-card-title>
                                    <span class="headline">User Profile</span>
                                </v-card-title>
                                <v-form lazy-validation ref="form">
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="First Name *"  required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Last Name *"  required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Username *"  required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field 
                                                        label="Password *" 
                        
                                                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                                        :type="show1 ? 'text' : 'password'"
                                                        @click:append="show1 = !show1"
                                                        required
                                                        ></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Contact No. *" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="6">
                                                    <v-text-field label="Agency *" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-select :items="userType"
                                                        label="User Type *" required></v-select>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                        <small>*indicates required field</small>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
                                        <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                                    </v-card-actions>
                                </v-form>
                            </v-card>
                        </v-dialog>
                        <v-dialog v-model="dialog2" persistent max-width="400px">
                            <v-card>
                                <v-card-title>
                                    <span class="headline">Set Role</span>
                                </v-card-title>
                                <v-card-text>
                                    <v-container>
                                        <v-row>
                                            <v-col cols="12">
                                                <v-select v-model="selectedRole" :items="userType"
                                                        label="User Type *" required></v-select>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="dialog2 = false">Close</v-btn>
                                    <v-btn color="blue darken-1" text @click="saveRole">Save</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </div>
                    <v-card class="mx-auto">
                        <v-simple-table dense class="px-2 py-2">
                            <template v-slot:default>
                                <thead>
                                    <tr>
                                        <th class="text-left">No.</th>
                                        <th class="text-left">Name</th>
                                        <th class="text-left">Agency</th>
                                        <th class="text-left">User Type</th>
                                        <th class="text-left">Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="user in users" :key="user.id">
                                        <td>{{ user.id }}</td>
                                        <td>{{ user.first_name }} {{ user.last_name }}</td>
                                        <td>{{ user.agency }}</td>
                                        <td>{{ user.name }}</td>
                                        <td>
                                            <div class="my-1">
                                                <v-btn small color="success" class="mx-2">Edit</v-btn>
                                                <v-btn v-if="user.name != null" small color="error" @click="removeRole(user.id, user.name)">
                                                    Remove role
                                                </v-btn>
                                                <v-btn v-if="user.name == null" small color="secondary" @click="setRole(user.id)">
                                                    Set role    
                                                </v-btn>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </template>
                        </v-simple-table>
                    </v-card>
                </v-col>
            </v-row>
            <v-snackbar v-model="snackbar" :color="color" :timeout=5000 :top="true">
                {{ snackbarText }}
                <v-btn dark text @click="snackbar = false">
                    Close
                </v-btn>
            </v-snackbar>
        </v-container>
    </v-content>
</template>

<script>

    export default {
        data() {
            return {
                user: {
                    username: '',
                    password: '',
                    agency: '',
                    first_name: '',
                    last_name: '',
                    contact: '',

                },
                color: null,
                snackbar: false,
                snackbarText: '',
                breadcrumbs: [
                {
                    text: 'Home',
                    disabled: true,
                    href: '#'
                },
                {
                    text: 'Dashboard',
                    disabled: true,
                    href: '#'
                },
                {
                    text: 'Users',
                    disabled: true,
                    href: '#'
                }
                ],
                users: [],
                userType: [
                    'admin',
                    'remote_user'
                ],
                userData: null,
                selectedRole: '',
                selectedId: '',
                show1: false,
                dialog: false,
                dialog2: false
            }
        },
        methods: {
            save()
            {

            },
            removeRole(id, role)
            {
                this.userData = {
                    id: id,
                    role: role
                }
                this.$store.dispatch('removeRole', this.userData)
                    .then(response => {
                        console.log(response)
                        this.users.find(person => person.id === id).name = null
                        this.userData = null
                        this.selectedRole = null
                        this.selectedId = null
                        this.dialog2 = false
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },
            setRole(id)
            {
                this.dialog2 = true
                this.selectedId = id
            },
            saveRole()
            {
                this.userData = {
                    id: this.selectedId,
                    role: this.selectedRole
                }
                this.$store.dispatch('setRole', this.userData)
                    .then(response => {
                        console.log(response)
                        this.users.find(person => person.id === this.selectedId).name = this.selectedRole
                        this.userData = null
                        this.selectedRole = null
                        this.selectedId = null
                        this.dialog2 = false
                    })
                    .catch(error => {
                        console.log(error)
                    })
            }
        },
        async created()
        {
            const data = await this.$store.dispatch('fetchUsers')
            this.users = data.data
            console.log(this.users)
        }
    }
</script>

