<template>
    <v-content>
        <v-container>
            <v-row>
                <v-col cols="12">
                    <h4 class="headline">Travel logs</h4>
                    <v-breadcrumbs :items="breadcrumbs">
                        <template v-slot:divider>
                            <v-icon>mdi-chevron-right</v-icon>
                        </template>
                    </v-breadcrumbs>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <div class="text-right mb-5">
                        <download-excel :data="profiles">
                            <v-btn depressed small color="success" class="ml-3">Download CSV</v-btn>
                        </download-excel>
                    </div>
                    <v-card>
                        <v-card-title class="font-weight-light mb-5">
                            Profiles
                            <v-spacer></v-spacer>
                            <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line
                                          hide-details></v-text-field>
                        </v-card-title>
                        <v-data-table :headers="headers" :items="profiles"  :search="search"></v-data-table>

                    </v-card>
                </v-col>
            </v-row>
        </v-container>
    </v-content>
</template>

<script>
    import JsonExcel from 'vue-json-excel'
    export default {
        components: {
            'downloadExcel': JsonExcel
        },
        name: "Travel",
        data() {
            return {
                breadcrumbs: [{
                    text: 'Home',
                    disabled: true,
                    href: '#'
                },
                    {
                        text: 'Dashboard',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Profiles',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Travels',
                        disabled: true,
                        href: '#'
                    }
                ],
                headers: [
                    { text: 'ID', align: 'start', sortable: true, value: 'id'},
                    { text: 'Name', sortable: true, value: 'name'},
                    { text: 'Age', sortable: true, value: 'age'},
                    { text: 'Gender', sortable: true, value: 'gender'},
                    { text: 'Type', sortable: true, value: 'type'},
                    { text: 'Address', sortable: true, value: 'address'},
                    { text: 'Contact', sortable: true, value: 'contact'},
                    { text: 'Origin', sortable: true, value: 'origin'},
                    { text: 'Destination', sortable: true, value: 'destination'},
                    { text: 'Departure', sortable: true, value: 'departure'},
                    { text: 'Arrival', sortable: true, value: 'arrival'},
                    { text: 'Transport Details', sortable: true, value: 'transport'},
                    { text: 'Seat No.', sortable: true, value: 'seat'},
                ],
                profiles: [],
                search: ''
            }
        },
        methods: {},
        async created()
        {
            const record = await this.$store.dispatch('travelLogs')
            this.profiles = record.data.map(function(person){
                let age = '-'
                if(person.profile.dob){
                    const date_array = person.profile.dob.split("-")
                    age = Math.floor((new Date() - new Date(date_array[0], date_array[1], date_array[2]))/(1000*60*60*24*365))
                }
                const address = `${person.profile.address.zipcodes.city}, ${person.profile.address.zipcodes.province}`
                return {
                    id: person.profile.id,
                    name: `${person.profile.first_name} ${person.profile.last_name}`,
                    age: (age > 0) ? age : '-',
                    gender: (person.profile.gender) ? person.profile.gender : '-',
                    type: (person.profile.person_type) ? person.profile.person_type : '-',
                    address: address,
                    contact: (person.profile.mobile) ? person.profile.mobile : '',
                    origin: person.fromLocation,
                    destination: person.toLocation,
                    departure: person.fromDate,
                    arrival: person.toDate,
                    transport: JSON.parse(person.vehicle_details),
                    seat: person.seat_no
                }
            })

        }
    }
</script>

