import Vue from 'vue'
import VueRouter from 'vue-router'
import Container from '../views/Container.vue'
import Login from '../views/Login.vue'
import Dashboard from '../views/Dashboard/Dashboard.vue'
import Adhoc from '../views/Reports/Adhoc.vue'
import Manage from '../views/Users/Manage.vue'
import Import from '@/views/Users/Import.vue'
import QRCode from '@/views/Profiles/QRCode.vue'
import Tracer from '@/views/Profiles/Tracer.vue'
import Travel from '@/views/Profiles/Travel.vue'
import Lgu from '@/views/Users/Lgu.vue'
import Carriers from '@/views/Users/Carriers.vue'
import store  from '../store'

Vue.use(VueRouter)

const routes = [{
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title: 'Login',
      requiresAuth: false
    }
  },
  {
    path: '/',
    redirect: '/dashboard',
    name: 'Container',
    component: Container,
    meta: {
      title: 'Container',
      requiresAuth: true
    },
    children: [
      {
        path: '/dashboard',
        component: Dashboard,
        meta: {
          title: 'Dashboard',
          requiresAuth: true
        }
      },
      {
        path: '/reports',
        component: Adhoc,
        meta: {
          title: 'Adhoc',
          requiresAuth: true
        }
      },
      {
        path: '/users',
        component: Manage,
        meta: {
          title: 'Users',
          requiresAuth: true
        }
      },
      {
        path: '/users/import',
        component: Import,
        meta: {
          title: 'Import',
          requiresAuth: true
        }
      },
      {
        path: '/users/lgu',
        component: Lgu,
        meta: {
          title: 'Lgu',
          requiresAuth: true
        }
      },
      {
        path: '/users/carriers',
        component: Carriers,
        meta: {
          title: 'Carriers',
          requiresAuth: true
        }
      },
      {
        path: '/profiles/qrcode',
        component: QRCode,
        meta: {
          title: 'QRCode',
          requiresAuth: true
        }
      },
      {
        path: '/profiles/trace',
        component: Tracer,
        meta: {
          title: 'Tracer',
          requiresAuth: true
        }
      },
      {
        path: '/profiles/travel',
        component: Travel,
        meta: {
          title: 'Travel',
          requiresAuth: true
        }
      }
    ]
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "about" */ '../views/About.vue'),
    meta: {
      title: 'About',
      requiresAuth: true
    }
  }
]


const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach(async (to, from, next) => {
  if(!to.meta.requiresAuth) return next()
  if(!localStorage.getItem('jwt-auth-token')) return router.replace('/login')
  if(typeof localStorage.getItem('jwt-auth-token') && localStorage.getItem('jwt-auth-token') === null) return router.replace('/login')
  let response = await store.dispatch('checkUser')
  if(!response.data.user){
    await store.dispatch('logout')
    return router.replace('/login')
  }
  return next()
})

export default router
