<template>
    <v-app id="inspire">
        <v-content>
            <v-container class="fill-height" fluid>
                <v-row align="center" justify="center">
                    <v-col cols="12" sm="8" md="4">
                        <v-card class="elevation-12" color="primary" light>
                            <div class="text-center">
                                <img src="@/assets/images/ustp.png" alt="" width="30%" class="mt-8">
                                <h3 class="white--text font-weight-light headline mt-5">USTP HIMS - LOGIN</h3>
                            </div>
                            <v-card-text>
                                <v-form @submit.prevent="onSubmit">
                                    <v-text-field solo label="Login" v-model="form.username" append-icon="mdi-account-key" type="text" required hint="Required">
                                    </v-text-field>

                                    <v-text-field solo id="password" label="Password" v-model="form.password" append-icon="mdi-lock"
                                        type="password" required loading="true" hint="Required"></v-text-field>
                                    <v-spacer></v-spacer>
                                    <div class="text-right">
                                        <v-btn type="submit" class="ma-2" tile outlined color="secondary">
                                            <v-icon left>mdi-login</v-icon>    Login
                                        </v-btn>
                                    </div>
                                </v-form>
                            </v-card-text>

                            <v-snackbar
                            v-model="snackbar"
                            :color="color"
                            :timeout=5000
                            :top="true"
                            >
                                {{ snackbarText }}
                                <v-btn
                                    dark
                                    text
                                    @click="snackbar = false"
                                >
                                    Close
                                </v-btn>
                            </v-snackbar>
                        </v-card>
                    </v-col>
                </v-row>
            </v-container>
        </v-content>
    </v-app>
</template>

<script>

    import { mapActions } from 'vuex'

    export default {
        data () {
            return {
                snackbar: false,
                snackbarText: '',
                color: "#E53935",
                form: {
                    username: '',
                    password: ''
                }
            }
        },
        methods: {
            ...mapActions(['login']),
            onSubmit(){
                this.login(this.form)
                .then((result) => {
                    if(result.data.status === false)
                    {
                        this.snackbarText = result.data.error
                        this.snackbar = true
                        return false
                    }
                    if(!Array.isArray(result.data.role) || !result.data.role.length)
                    {
                        this.snackbarText = "User is unauthorize to access the system"
                        this.snackbar = true
                        return false
                    }else{
                        if(result.data.role[0] != 'admin'){
                            this.snackbarText = "User is unauthorize to access the system"
                            this.snackbar = true
                            return false
                        }
                    }
                    this.$store.commit('LOGIN_SUCCESSFUL',result.data)
                    return this.$router.replace('/dashboard')
                })
                .catch(error => {
                    this.snackbarText = error + ". Please contact the administrator"
                    this.snackbar = true
                })
            }
        }
    }
</script>
