import axios from 'axios'

const state = {
    data: [],
    barangay: [],
    symptoms: [],
    average: null,
    profileToday: null
}

const getters = {
    allBarangay: state => state.barangay,
    allSymptoms: state => state.symptoms,
    averageProfile: state => state.average,
    profileToday: state => state.profileToday
}

const actions = {
    async fetchBarangay({ commit }){
        const response = await axios.get('/master/barangay')
        commit('setBarangays',response.data)
    },
    async fetchSymptoms({ commit }){
        const response = await axios.get('/master/conditions')
        commit('setConditions', response.data)
    },
    importUser(context,data){
        let formData = new FormData()
        formData.append('userFile', data)
        return axios.post('/store/import/users',formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    },
    async fetchAverageProfile({ commit })
    {
        const response = await axios.get('/retrieve/average')
        commit('setAverage', response.data)
    },
    async fetchProfileToday({ commit })
    {
        const response = await axios.get('/retrieve/today')
        commit ('setProfileToday', response.data.length)
    },
    fetchTotalRecords()
    {
        return axios.get('/retrieve/total')
    },
    fetchMapPoints()
    {
        return axios.get('/retrieve/map-points')
    },
    fetchHeaders(context, option)
    {
        return axios.get(`/retrieve/get-columns/${option}`);
    },
    fetchReports(context, data)
    {
        return axios.post('/retrieve/generate/reports', data)
    },
    fetchProfiles()
    {
        return axios.get('/retrieve/all-profiles')
    },
    fetchProvince()
    {
        return axios.get('/retrieve/provinces');
    },
    fetchLGU()
    {
        return axios.get('/retrieve/lgu');
    },
    saveLGU(context, data)
    {
        return axios.post('/store/register/lgu', data)
    },
    setActive(context, data)
    {
        return axios.get(`/store/lgu/enable/${data}`)
    },
    setInactive(context, data)
    {
        return axios.get(`/store/lgu/disable/${data}`)
    },
    fetchCarriers()
    {
        return axios.get('/retrieve/carriers')
    },
    setCarrierActive(context, data)
    {
        return axios.get(`/store/carrier/enable/${data}`)
    },
    setCarrierInactive(context, data)
    {
        return axios.get(`/store/carrier/disable/${data}`)
    },
    saveCarrier(context, data)
    {
        return axios.post('/store/register/carrier', data)
    },
    fetchUsers()
    {
        return axios.get('/retrieve/all-users')
    },
    setRole(context, data)
    {
        return axios.post('/store/set-role', data)
    },
    removeRole(context, data)
    {
        return axios.post('/store/remove-role', data)
    },
    traceLogs() {
        return axios.get('/retrieve/trace-logs')
    },
    profilePerDay()
    {
        return axios.get('/retrieve/per-day');
    },
    forBarChart()
    {
        return axios.get('/retrieve/per-province')
    },
    travelLogs()
    {
        return axios.get('/retrieve/travel-logs')
    },
    updateCarrier(context, data)
    {
        return axios.post('/auth/carrier/update', data);
    },
    updateLGU(context, data)
    {
        return axios.post('/auth/lgu/update', data);
    }
}

const mutations = {
    setBarangays: (state, barangays) => (state.barangay = barangays),
    setConditions: (state, conditions) => (state.symptoms = conditions),
    setAverage: (state, data) => (state.average = data),
    setProfileToday: (state, data) => (state.profileToday = data)
}

export default {
    state,
    getters,
    actions,
    mutations
}
