import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import router from './router'
import store from './store'
import VueHtmlToPaper from 'vue-html-to-paper'
import Chartkick  from "vue-chartkick";
import Chart from 'chart.js'

window._ = require('lodash')
window.axios = require('axios')
window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest'
window.axios.interceptors.request.use(function (config) {
  config.headers.common['Authorization'] = `Bearer ${localStorage.getItem('jwt-auth-token')}`
  return config
}, function (error) {
  return Promise.reject(error)
})
//window.axios.defaults.baseURL = 'http://127.0.0.1:8000/api/v1'
//window.axios.defaults.baseURL = 'http://10.10.10.16/covid19tracker-api/public/api/v1'
window.axios.defaults.baseURL = 'http://covid.ustp.edu.ph/covid19tracker-api/public/api/v1'

Vue.config.productionTip = false

const options = {
  name: '_blank',
  specs: [
    'fullscreen=yes',
    'titlebar=yes',
    'scrollbars=yes'
  ],
  styles: [
    "https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css",
    "./assets/styles/custom.css"
  ]
}
Vue.use(Chartkick.use(Chart))
Vue.use(VueHtmlToPaper, options)


new Vue({
  vuetify,
  router,
  store,
  render: h => h(App)
}).$mount('#app')
