<template>
    <v-content>
        <v-container fluid>
            <v-row>
                <v-col cols="6">
                    <v-form @submit.prevent="upload">
                        <v-file-input label="File input" filled prepend-icon="mdi-camera" v-model="files">
                        </v-file-input>
                        <v-spacer></v-spacer>

                        <div class="text-right">
                            <v-btn color="primary" large type="submit">
                                <v-icon left>mdi-import</v-icon> Import
                            </v-btn>
                        </div>
                    </v-form>

                </v-col>
            </v-row>
            <v-snackbar v-model="snackbar" :color="color" :timeout=5000 :top="true">
                {{ snackbarText }}
                <v-btn dark text @click="snackbar = false">
                    Close
                </v-btn>
            </v-snackbar>
        </v-container>
    </v-content>
</template>

<script>
    import {
        mapActions
    } from 'vuex'

    export default {
        data() {
            return {
                files: [],
                color: null,
                snackbar: false,
                snackbarText: ''
            }
        },
        methods: {
            ...mapActions(['importUser']),
            upload() {
                this.importUser(this.files)
                    .then((response) => {
                        if(response.data.status === false)
                        {
                            this.snackbarText = response.data.message
                            this.snackbar = true
                            this.color = '#E53935'
                            this.files = []
                            return false
                        }
                        this.snackbarText = response.data.message
                        this.snackbar = true
                        this.color = '#43A047'
                    })
            },
        }
    }
</script>

