<template>
    <v-content>
        <v-container fluid>
            <v-row>
                <v-col cols="12">
                    <h4 class="headline">Carriers</h4>
                    <v-breadcrumbs :items="breadcrumbs">
                        <template v-slot:divider>
                            <v-icon>mdi-chevron-right</v-icon>
                        </template>
                    </v-breadcrumbs>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <div class="mb-5 text-right">
                        <v-dialog v-model="dialog2" persistent max-width="600px">
                            <v-card>
                                <v-card-title>
                                    <span class="headline">Edit Transport Carrier Profile</span>
                                </v-card-title>
                                <v-form lazy-validation ref="form1">
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12">
                                                    <v-text-field label="Name *" v-model="editData.carrier" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-text-field label="Contact No. *" v-model="editData.contact" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-text-field label="Address *" v-model="editData.address" required>
                                                    </v-text-field>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                        <small>*indicates required field</small>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="dialog2 = false">Close</v-btn>
                                        <v-btn color="blue darken-1" text @click="update">Save</v-btn>
                                    </v-card-actions>
                                </v-form>
                            </v-card>
                        </v-dialog>
                        <v-dialog v-model="dialog" persistent max-width="600px">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn color="primary" dark v-bind="attrs" v-on="on">
                                    Add new carrier
                                </v-btn>
                            </template>
                            <v-card>
                                <v-card-title>
                                    <span class="headline">Transport Carrier Profile</span>
                                </v-card-title>
                                <v-form lazy-validation ref="form">
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12">
                                                    <v-text-field label="Name *" v-model="data.carrier_name" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-text-field label="Password *" v-model="data.password"
                                                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                                        :type="show1 ? 'text' : 'password'"
                                                        @click:append="show1 = !show1" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-text-field label="Contact No. *" v-model="data.carrier_contact" required>
                                                    </v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-text-field label="Address *" v-model="data.carrier_address" required>
                                                    </v-text-field>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                        <small>*indicates required field</small>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
                                        <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                                    </v-card-actions>
                                </v-form>
                            </v-card>
                        </v-dialog>
                    </div>
                    <v-card class="mx-auto">
                        <v-simple-table dense class="px-2 py-2">
                            <template v-slot:default>
                                <thead>
                                    <tr>
                                        <th class="text-left">No.</th>
                                        <th class="text-left">Name</th>
                                        <th class="text-left">Address</th>
                                        <th class="text-left">Contact</th>
                                        <th class="text-left">Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="carrier in carriers" :key="carrier.id">
                                        <td>{{ carrier.id }}</td>
                                        <td>{{ carrier.carrier_name }}</td>
                                        <td>{{ carrier.carrier_address }}</td>
                                        <td>{{ carrier.carrier_contact }}</td>
                                        <td>
                                            <div class="my-1">
                                                <v-btn small color="success" class="mx-2" @click="edit(carrier.id)">Edit</v-btn>
                                                <v-btn v-if="carrier.isActive == 1" small color="error" @click="disable(carrier.id)">
                                                    Set inactive
                                                </v-btn>
                                                <v-btn v-if="carrier.isActive == 0" small color="secondary" @click="enable(carrier.id)">
                                                    Set Active
                                                </v-btn>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </template>
                        </v-simple-table>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
        <v-snackbar v-model="snackbar" :color="color" :top="true" :vertical="true">
            {{ snackbarText }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-content>
</template>

<script>

    import randomstring from 'randomstring'
    export default {
        name: 'Carriers',
        data() {
            return {
                breadcrumbs: [{
                        text: 'Home',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Dashboard',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Users',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Carriers',
                        disabled: true,
                        href: '#'
                    }
                ],
                data: {
                    carrier_name: '',
                    carrier_contact: '',
                    carrier_address: '',
                    password: ''
                },
                editData: {
                  id: '',
                  carrier: '',
                  contact: '',
                  address: ''
                },
                carriers: [],
                dialog: false,
                dialog2: false,
                show1: false,
                snackbar: false,
                snackbarText: '',
                color: "#E53935",
            }
        },
        methods: {
            save()
            {
                this.$store.dispatch('saveCarrier', this.data)
                    .then(response => {
                        if(response.data.error){
                            this.snackbarText = ''
                            for(let [key,value] of Object.entries(response.data.error))
                            {
                                console.log("key", key)
                                this.snackbarText += value[0] + '. '

                            }

                            this.snackbar = true
                            return false
                        }

                        this.carriers.push(response.data.carrier)
                        this.color = '#3CB371',
                        this.snackbarText = response.data.message
                        this.snackbar = true
                        this.data.name = ''
                        this.data.contact = ''
                        this.data.address = ''
                        this.data.password = ''
                    })
                    .catch((err) => {
                        console.log(err)
                    })
                this.dialog = false
            },
            enable(id)
            {
                this.$store.dispatch('setCarrierActive', id)
                    .then(response => {
                        if(response.data.status === false) return false
                        this.carriers.find(carrier => carrier.id === id).isActive = 1
                    })
            },
            disable(id)
            {
                this.$store.dispatch('setCarrierInactive', id)
                    .then(response => {
                        if(response.data.status === false) return false
                        this.carriers.find(carrier => carrier.id === id).isActive = 0
                    })
            },
            edit(id)
            {
                this.editData.id = id
                this.editData.carrier = this.carriers.find(carrier => carrier.id === id).carrier_name
                this.editData.address = this.carriers.find(carrier => carrier.id === id).carrier_address
                this.editData.contact = this.carriers.find(carrier => carrier.id === id).carrier_contact
                this.dialog2 = true
            },
            update()
            {
                this.$store.dispatch('updateCarrier', this.editData)
                    .then(response => {
                        if(response.data.status === false) return false
                        this.carriers.find(carrier => carrier.id === this.editData.id).carrier_name = this.editData.carrier
                        this.carriers.find(carrier => carrier.id === this.editData.id).carrier_contact = this.editData.contact
                        this.carriers.find(carrier => carrier.id === this.editData.id).carrier_address = this.editData.address
                    })
                this.dialog2 = false
            }
        },
        async created()
        {
            const data = await this.$store.dispatch('fetchCarriers')
            this.carriers = data.data

            this.data.password = randomstring.generate(12)
        }
    }
</script>

<style>

</style>
