<template>
    <v-content>
        <v-container fluid>
            <v-row>
                <v-col cols="12">
                    <h4 class="headline">Reports</h4>
                    <v-breadcrumbs :items="breadcrumbs">
                        <template v-slot:divider>
                            <v-icon>mdi-chevron-right</v-icon>
                        </template>
                    </v-breadcrumbs>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="4" class="pt-0">
                    <v-row>
                        <v-col cols="12">
                            <v-card>
                                <v-card-title class="primary white--text title font-weight-light mb-5">Report Type
                                </v-card-title>
                                <v-card-text>
                                    <v-select v-model="reportType" :items="reports" label="Select Report" solo
                                        @change="selectHeader($event)"></v-select>
                                </v-card-text>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="12">
                            <v-card>
                                <v-card-title class="primary white--text title font-weight-light mb-5">Filter Category
                                </v-card-title>
                                <v-card-text>
                                    <v-expansion-panels>
                                        <v-expansion-panel>
                                            <v-expansion-panel-header>Gender</v-expansion-panel-header>
                                            <v-expansion-panel-content>
                                                <v-checkbox v-model="selectedGender" label="Male" value="male">
                                                </v-checkbox>
                                                <v-checkbox v-model="selectedGender" label="Female" value="female">
                                                </v-checkbox>
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>

                                        <v-expansion-panel v-if="reportType != 'travel'">
                                            <v-expansion-panel-header>Symptoms</v-expansion-panel-header>
                                            <v-expansion-panel-content>
                                                <v-select v-model="selectedSymptoms" :items="symptoms"
                                                    label="Select Symptos" multiple chips
                                                    hint="What are the target symptoms" persistent-hint></v-select>
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>

                                        <v-expansion-panel>
                                            <v-expansion-panel-header>Date</v-expansion-panel-header>
                                            <v-expansion-panel-content>
                                                <v-date-picker v-model="dates" range></v-date-picker>
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>
                                    </v-expansion-panels>
                                </v-card-text>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="12">
                            <v-card>
                                <v-card-title class="primary white--text title font-weight-light mb-5">Order Fields
                                </v-card-title>
                                <v-card-text>
                                    <draggable
                                            :list="selectedFields"
                                            :disabled="!enabled"
                                            class="list-group"
                                            ghost-class="ghost"
                                            @start="dragging = true"
                                            @end="dragging = false"
                                    >
                                        <v-list-item v-for="(element, index) in selectedFields"
                                                     :key="index" style="border: lightgray solid 1px; border-radius: 2px; margin-bottom: 1px">
                                            <v-list-item-content>
                                                <v-list-item-title>
                                                    {{ element }}
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </draggable>
                                </v-card-text>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-col>
                <v-col cols="8">
                    <v-card>
                        <v-card-title class="primary white--text title font-weight-light mb-5">Filter Fields
                        </v-card-title>

                        <v-card-text>
                            <div class="text-right">
                            <v-btn depressed small :disabled="enableBtn"  color="primary" class="ml-3" @click="selectAll">Select all
                            </v-btn>
                            <v-btn depressed small :disabled="enableBtn" color="secondary" class="ml-3" @click="unSelect">Unselect all</v-btn>
                        </div>
                            <v-container fluid>
                                <v-layout row wrap>
                                    <v-flex v-for="(value, name, index) in fields" :key="index" xs4>
                                        <v-checkbox class="mx-2" :value="name" :label=value v-model="selectedFields">
                                        </v-checkbox>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-card-text>
                    </v-card>
                    <div class="text-right mt-5">
                        <v-btn depressed large color="primary" class="ml-3" v-on:click="generateReports">Generate
                        </v-btn>
                    </div>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <div class="text-right mb-5" v-if="downloadCSV">
                        <download-excel :data="profile">
                            <v-btn depressed small color="success" class="ml-3">Download CSV</v-btn>
                        </download-excel>
                    </div>
                    <v-card>
                        <v-card-title class="font-weight-light mb-5">
                            Reports
                            <v-spacer></v-spacer>
                            <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line
                                hide-details></v-text-field>
                        </v-card-title>
                        <v-data-table :headers="headers" :items="profile" :search="search"></v-data-table>
                    </v-card>
                </v-col>
            </v-row>
            <v-snackbar v-model="snackbar" :color="color" :timeout=5000 :top="true">
                {{ snackbarText }}
                <v-btn dark text @click="snackbar = false">
                    Close
                </v-btn>
            </v-snackbar>
        </v-container>
    </v-content>
</template>

<script>
    import {
        mapActions,
        mapGetters
    } from 'vuex'
    import draggable from 'vuedraggable'
    import JsonExcel from 'vue-json-excel'

    export default {
        components: {
            draggable,
            'downloadExcel': JsonExcel
        },
        data() {
            return {
                breadcrumbs: [{
                        text: 'Home',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Dashboard',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Reports',
                        disabled: true,
                        href: '#'
                    }
                ],
                reports: [{
                        value: 'master',
                        text: 'Master List'
                    },
                    {
                        value: 'travel',
                        text: 'Travel Histories'
                    },
                    {
                        value: 'employment',
                        text: 'Employment Details',
                        disabled: true
                    },
                    {
                        value: 'contacts',
                        text: 'Person in contacts',
                        disabled: true
                    },
                    {
                        value: 'conditions',
                        text: 'Medical Conditions',
                        disabled: true
                    },
                    {
                        value: 'symptoms',
                        text: 'Stats - Symptoms',
                        disabled: true
                    },
                    {
                        value: 'facility',
                        text: 'Facility histories',
                        disabled: true
                    },
                    {
                        value: 'qurantine',
                        text: 'Quarantine Details',
                        disabled: true
                    },
                ],
                optionData: {},
                selectedGender: [],
                selectedSymptoms: [],
                dates: [],
                reportType: null,
                symptoms: [],
                headers: [],
                profile: [],
                fields: [],
                selectedFields: [],
                search: '',
                snackbar: false,
                snackbarText: '',
                color: "#E53935",
                enableBtn: true,
                enabled: true,
                downloadCSV: false,
            }
        },
        methods: {
            ...mapActions(['fetchSymptoms']),
            generateReports() {
                this.optionData = {
                    fields: this.selectedFields,
                    gender: this.selectedGender,
                    symptoms: this.selectedSymptoms,
                    dates: this.dates,
                    type: this.reportType
                }
                this.$store.dispatch('fetchReports', this.optionData)
                    .then((response) => {
                        if(response.data.status === false)
                        {
                            this.snackbarText = response.data.message
                            this.snackbar = true
                            return false
                        }
                      this.headers = this.selectedFields.map(d => ({
                          text: d.replace("_"," ").toUpperCase(),
                          sortable: true,
                          value: d
                      }))
                      this.profile = response.data
                      this.downloadCSV = true
                    })
            },
            selectHeader(event) {
                this.fields = []
                this.$store.dispatch('fetchHeaders', event)
                    .then((response) => {
                        this.fields = response.data
                        this.enableBtn = false;
                    })
                this.downloadCSV = false
            },
            selectAll() {
                this.selectedFields = Object.keys(this.fields)
            },
            unSelect(){
                this.selectedFields = []
            }
        },
        computed: mapGetters(['allSymptoms']),
        async created() {
            await this.fetchSymptoms()
            this.symptoms = this.allSymptoms.map((d) => ({
                value: d.id,
                text: d.condition
            }))
        },
    }
</script>

<style>

</style>
