<template>
    <v-content>
        <v-container>
            <v-row>
                <v-col cols="12">
                    <h4 class="headline">Print QR codes</h4>
                    <v-breadcrumbs :items="breadcrumbs">
                        <template v-slot:divider>
                            <v-icon>mdi-chevron-right</v-icon>
                        </template>
                    </v-breadcrumbs>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="6">
                    <multiselect v-model="value" :options="options" :multiple="true" :close-on-select="false"
                        label="first_name" :custom-label="customLabel" :clear-on-select="false" :preserve-search="true"
                        :show-labels="false" track-by="id" :preselect-first="true" class="mb-5 mt-1" placeholder="Select Profiles">
                        <template slot="option" slot-scope="props">
                            <div class="option__desc"><span
                                    class="option__title">{{ props.option.last_name + ", " + props.option.first_name}}</span>
                            </div>
                        </template>
                        <template slot="selection" slot-scope="{ values, search, isOpen }"><span
                                class="multiselect__single" v-if="values.length &amp;&amp; !isOpen">{{ values.length }}
                                options selected</span></template>
                    </multiselect>
                </v-col>
                <v-col cols="4">
                    <v-btn class="ma-2" tile outlined small color="success" @click="refreshList">
                        <v-icon left>mdi-refresh</v-icon> Refresh list
                    </v-btn>
                    <v-btn class="ma-2" small tile outlined color="primary" @click="printQr" v-if="this.value.length > 0">
                        <v-icon left>mdi-printer</v-icon> Print
                    </v-btn>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <div id="printQr">
                        <v-container id="scroll-target" style="max-height: 400px" class="overflow-y-auto">
                            <v-row v-scroll:#scroll-target>
                                <v-col v-for="(value, key) in value" :key="key" cols="6">
                                <v-card light class="my-3">
                                    <v-list-item class="mt-5 mb-1">
                                        <img class="float-left pa-2" src="../../assets/images/ustp-dark-logo.png" width="10%" alt="">
                                        <v-list-item-content>
                                            <v-list-item-title class="text-md-h3 font-weight-bold">USTP COVID19 Tracker
                                            </v-list-item-title>
                                            <v-list-item-subtitle>by Digital Transformation - USTP CDO
                                            </v-list-item-subtitle>
                                        </v-list-item-content>
                                    </v-list-item>
                                    <hr>
                                    <div class="container-fluid">
                                        <div class="row">
                                            <div class="col-md-8">
                                                <div>
                                                    <h3 class="headline">{{ value.first_name }} {{ value.last_name }}</h3>
                                                    <p>
                                                        <strong>Contact: </strong><span>{{ value.mobile }}</span><br>
                                                        <strong>Address: </strong><span v-if="value.address != null">
                                                                {{ value.address.zipcodes.city }},
                                                                {{ value.address.zipcodes.province }}</span>
                                                    </p>
                                                </div>
                                            </div>

                                            <div class="col-md-4 text-right">
                                                <qrcode-vue :value="value.person_type + '-' + value.id + '-' + value.first_name + '-' + value.last_name"
                                                        size="130" level="H" renderAs="svg">
                                                </qrcode-vue>
                                            </div>
                                        </div>
                                    </div>
                                </v-card>
                                </v-col>
                            </v-row>
                        </v-container>
                    </div>
                </v-col>
            </v-row>
        </v-container>
        <v-snackbar
            v-model="snackbar"
            :color="color"
            :timeout=5000
            :top="true"
            >
                {{ snackbarText }}
            <v-btn
            dark
            text
            @click="snackbar = false"
            >
            Close
            </v-btn>
        </v-snackbar>
    </v-content>
</template>

<script>
    import Multiselect from 'vue-multiselect'
    import QrcodeVue from 'qrcode.vue'

    export default {
        components: {
            Multiselect,
            QrcodeVue
        },
        data() {
            return {
                snackbar: false,
                snackbarText: '',
                color: "#E53935",
                breadcrumbs: [{
                        text: 'Home',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Dashboard',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Profiles',
                        disabled: true,
                        href: '#'
                    },
                    {
                        text: 'Print QR',
                        disabled: true,
                        href: '#'
                    }
                ],
                value: [],
                options: []
            }
        },
        methods: {
            customLabel({first_name,last_name}) {
                return `${ last_name }, ${first_name}`
            },
            async refreshList()
            {
                this.options = []
                const response = await this.$store.dispatch('fetchProfiles')
                this.options = response.data
            },
            printQr()
            {
                //window.print()
                if(this.value.length < 1){
                    this.snackbarText = "Please select profiles."
                    this.snackbar = true
                    return false
                }
                this.$htmlToPaper('printQr')
            }
        },
        async created() {
            const response = await this.$store.dispatch('fetchProfiles')
            this.options = response.data
        }
    }
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style>

</style>
